@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Georgia, "Times New Roman", Times, serif;
  color: #343a40;
}

.note {
  color: red;
  float: right;
  text-align: right;
  display: block;
}
.replacement {
  color: red;
}
.card-groupp {
  align-items: center;
  justify-content: center;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
}

.card-bodyy {
  display: inline-block;
  align-items: center;
  justify-content: center;
  border: solid 1px #343a40;
  border-radius: 1rem;
  margin: 0.2rem;
  padding: 1.5rem;
}

.card-subtitle {
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #6c757d;
}

.container {
  max-width: 98%;
  margin: 1rem auto;
  overflow: auto;
  min-height: 300px;
  border: 1px solid #343a40;
  padding: 2rem;
  border-radius: 5px;
}

.center-container{
  max-width: 500px;
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border: 1px solid steelblue;
  padding: 30px;
  border-radius: 5px;
}

.menu-card {
  display: inline-block;
  text-align: center;
  padding: 1rem 4rem;

  align-items: center;
  justify-content: center;
}


.header {
  text-align: center;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 3rem;
  padding-top: 1rem;
  font-family: Georgia, "Times New Roman", Times, serif;
}
.main-header{
  color: #28a745
}
.btn {
  float: right;
  text-align: right;
  display: inline;
  color: #fff;
  border: none;
  padding: .5%;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 1.05em;
  font-family: Georgia, 'Times New Roman', Times, serif;
  padding: 0.4rem;
}

.horizontal-line{
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.task {
  background: #f4f4f4;
  margin: 5px;
  padding: 10px 20px;
  cursor: pointer;
}

.task.reminder {
  border-left: 5px solid green;
}

.task h3 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.add-form {
  margin-bottom: 40px;
}

.form-control {
  margin: 20px 0;
}

.form-control label {
  display: block;
}

.form-control input {
  width: 100%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}

.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-control-check label {
  flex: 1;
}

.form-control-check input {
  flex: 2;
  height: 20px;
} 

footer {
  padding-top: 2rem;
  margin-top: 2rem;
  text-align: center;
}
